@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif&family=Protest+Riot&family=Sora:wght@400;500&display=swap');


body {
  text-align: center;
  font-family: 'Sora', sans-serif;
  color: #333333;
}

h1 {
  color: #3535a1;
}

table {
  margin: auto;
  border: 2px solid #110957;
}

th, td {
  padding: 6px;
  text-align: left;
}

.player-column {
  padding-right: 25px;
}

td.sets {
  text-align: right;
}

td.elo {
  text-align: right;
}

td.cell-align-right {
  text-align: right;
}

td.pos {
  color: rgb(0, 85, 0);
}

td.neg {
  color: rgb(95, 0, 0);
}

a {
  text-decoration: none;
  color: #12129c;
}

a:hover {
  color: #8888ff;
  transition-duration: 100ms;
}

.credits {
  margin-top: 25px;
}

.credits-line {
  margin-bottom: 20px;
}

.startgg-link {
  margin: 10px;
}

.dev {
  margin: 0 auto;
}

.dev form {
  margin-top: 20px;
}

input {
  padding: 8px;
  margin: 0 10px;
}

button, input[type="submit"] {
  padding: 8px;
  border-radius: 3px;
  transition-duration: 200ms;
}

button:hover, input[type="submit"]:hover {
  color: #ddd;
  background-color: #12129c;
  cursor: pointer;
  transition-duration: 100ms;
}

.home-link {
  margin-bottom: 20px;
}

.logout-link {
  margin-bottom: 20px;
}

.player-details-links {
  margin-bottom: 20px;
}

input[type='checkbox'] {
  width: 15px;
  height: 15px;
  background: white;
  border-radius: 5px;
  border: 2px solid #555;
  cursor: pointer;
}

input[type='checkbox']:checked {
  background: #abd;
}

.flex-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.make-invisible, .make-visible {
  margin: 10px;
}

.dev-tournament {
  padding: 0;

}

.dev-tournament-form {
  width: block;
  margin: 10px;
}

.dev-tournament-form input[type="text"] {
  width: 50%;
  max-width: 340px;
}

.dev-tournament-form select {
  padding: 10px 5px;
}

.dev-tournament-options {
  margin: 10px;
}

.dev-tournament-options button {
  margin: 5px;
}

.dev-reset-database {
  color: #fff;
  background-color: #aa0b0b;
}

.dev-reset-database:hover {
  background-color: #e74f4f;
}

.loader {
  border: 5px solid #f3f3f3; 
  border-top: 5px solid #12129c; 
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 10px auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.docs p {
  text-align: left;
  font-size: 18px;
}